// Scripts
const hamburger = document.querySelector(".hamburger");
const hamName = document.querySelector(".hamburger__name")
const navigation = document.querySelector(".navigation");
const menu = document.querySelector(".navigation__list");
const links = document.querySelectorAll(".navigation__item-link");
// const logo = document.querySelector(".logo__link");
const logoWhite = document.querySelector(".logo__white");
const logoBlue = document.querySelector(".logo__blue");

hamburger.addEventListener('click', () => {
    hamburger.classList.toggle('is-active');
    menu.classList.toggle('menu-active');
    logoWhite.classList.toggle('hide');
    logoBlue.classList.toggle('hide');
    hamName.classList.toggle('hamcolor');
    // logo.classList.toggle('color');

})

//GSAP

gsap.to(".bground--one", {
    duration: 2.5,
    ease: Power3.easeOut,
    delay: 0.5,
    y: "100%"
});
gsap.to(".bground--two", {
    duration: 2.5,
    ease: Power3.easeOut,
    delay: 0.5,
    y: "-100%"
});
gsap.fromTo(".logo__enter", {
    opacity: 1
}, {
    duration: 0.1, 
    delay: 0.9, 
    opacity: 0
});

links.forEach(link => {
    link.addEventListener("click", slide);
})

function slide() {
    gsap.fromTo (".slide", {x: "100%"},{
        x:"0", 
        duration: 1, 
        ease: Power3.easeOut,
    });
    gsap.fromTo (".slide--two", {x:"100%"}, {
        x:"0", 
        duration: 1, 
        ease: Power3.easeOut,
    });
    gsap.to (".slide", {y: "100%", duration: 2, delay: 0.5, ease: Power3.easeOut});
    gsap.to (".slide--two", {y: "-100%", duration: 2, delay: 0.5, ease: Power3.easeOut});
}
